<script>
import {ref} from 'vue'

export default {
  setup() {
    let itemData = [
      {id: 1, word: '[to give keys]', list: 13},
      {id: 2, word: 'locateReservation', list: 13},
      {id: 3, word: '[vehicle available]', list: 13},
      {id: 4, word: 'releaseContract', list: 13},
      {id: 5, word: 'updateKeyStatus', list: 13},
      {id: 6, word: 'initiateContract', list: 13},
      {id: 7, word: '[key handed over]', list: 13},
      {id: 8, word: '[wait for Vehicle]', list: 13},
      {id: 9, word: 'requestVehicle', list: 13},
      {id: 10, word: '[vehicle on trip]', list: 13},
      {id: 11, word: 'signContract', list: 13},
      {id: 12, word: '[reservation released]', list: 13},
    ]

    //shuffle data randomly
    itemData = itemData
        .map(value => ({ value, sort: Math.random() }))
        .sort((a, b) => a.sort - b.sort)
        .map(({ value }) => value)

    const items = ref(itemData)

    const getList = (list) => {
      return items.value.filter((item) => item.list === list)
    }

    const startDrag = (event, item) => {
      console.log(item)
      event.dataTransfer.setData('itemID', item.id)
      event.dataTransfer.effectAllowed = 'move'
      event.dataTransfer.dropEffect = 'move'
    }

    const onDrop = (event, list) => {
      event.preventDefault()
      const itemID = event.dataTransfer.getData('itemID')
      const item = items.value.find((item) => item.id === parseInt(itemID))
      item.list = list
    }

    return {
      getList,
      startDrag,
      onDrop
    }
  },
  data() {
    return {
      valid: true,
      visibility: [true, false, false, false]
    }
  },
  methods: {
    incorrect(msg) {
      var x = document.getElementById("snackbar");
      x.innerHTML = msg;
      x.style.backgroundColor = "red";
      x.className = "show";
      setTimeout(function () {
        x.className = x.className.replace("show", "");
      }, 3000)
    },
    validateCustomer() {
      
      let acquire = []
      let signs = []

      for (let i = 0; i < this.getList(1).length; i++) {
        acquire.push(this.getList(1)[i].word);
      }

      //Customer
      for (let i = 0; i < this.getList(2).length; i++) {
        signs.push(this.getList(2)[i].word)
      }

      if (acquire.includes('[wait for Vehicle]') && acquire.includes('requestVehicle') && signs.includes('signContract')) {
        this.visibility[0] = false
        this.visibility[1] = true
      } else {
        this.incorrect('Customer fields are incorrect. Please try again')
      }
    },
    validateClerk() {
      let locate = []
      let mark = []

      for (let i = 0; i < this.getList(3).length; i++) {
        locate.push(this.getList(3)[i].word);
      }

      //Clerk
      for (let i = 0; i < this.getList(4).length; i++) {
        mark.push(this.getList(4)[i].word); 
      }

      if (locate.includes('locateReservation') && mark.includes('initiateContract') && mark.includes('releaseContract') && mark.includes('updateKeyStatus') && mark.includes('[key handed over]')){
        this.visibility[1] = false
        this.visibility[2] = true
      } else {
        this.incorrect('Clerk fields are incorrect. Please try again')
      }
    },
    validateVehicle() {
      let give = []
      for (let i = 0; i < this.getList(5).length; i++) {
        give.push(this.getList(5)[i].word)
      }
      if (give.includes('[to give keys]')) {
        this.visibility[2] = false
        this.visibility[3] = true
      } else {
        this.incorrect('Vehicle field is incorrect. Please try again')
      }
    },
    validateReservation() {
      let vehicle = []
      for (let i = 0; i < this.getList(6).length; i++) {
        vehicle.push('this.getList(6)[i].word')
      }
      if (vehicle.includes('[vehicle on trip]') && vehicle.includes('[vehicle available]') && vehicle.includes('[reservation released]')) {
        this.visibility[3] = false
        this.visibility[4] = true
        //push
        this.$router.push('/anim-two')
      } else {
        this.incorrect('Reservation field is incorrect. Please try again')
      }
    },
    popup() {
            var modal = document.getElementById("myModal");

            // Get the button that opens the modal
            var btn = document.getElementById("myBtn");

            // Get the <span> element that closes the modal
            var span = document.getElementsByClassName("close")[0];

            // When the user clicks the button, open the modal 
            btn.onclick = function () {
                modal.style.display = "block";
            }

            // When the user clicks on <span> (x), close the modal
            span.onclick = function () {
                modal.style.display = "none";
            }

            // When the user clicks anywhere outside of the modal, close it
            window.onclick = function (event) {
                if (event.target == modal) {
                    modal.style.display = "none";
                }
            }
        },
  }
}
</script>


<template class="app">

  <header style="padding: 20px; display: flex; flex-direction: row; align-items: center; justify-content: space-between;">
    <div>
    <h1>Step 3: Finetuning verb phrases</h1>
    <p>To further finetune the identified verbs and to convert
      them to a programmable format, identify the relevant
      functions for the given verb phrases by dragging and
      dropping.</p>
    </div>
      <button id="myBtn" style="align-self: flex-end, margin-right: 20px;" @click="popup()" class="button-30">Help</button>
  </header>

  <main>
    <div id="snackbar">Some text some message..</div>

    <div id="myModal" class="modal">

<!-- Modal content -->
<div class="modal-content">
    <span class="close">&times;</span>
    <p style="padding: 25px;">The following is a demonstration of the interactivity.</p>
    <img src="..\assets\exp2s3.gif" style="height: 500px;">
</div>


</div>

    <div class="flex">
      <div class="flex-horizontal">
        <div class="outer-box">

          <div class="flex-boxes" :style="{display: visibility[0] ? 'block' : 'none'}">
            <div class="flex-between">
              <h3>Customer</h3>
              <div class="unidentified drag-el-horizontal" draggable="false">
                <p style="font-weight: 500; padding-left: 15px; font-size:medium;">Acquire a vehicle</p>

                <div class="drop-zone" @drop="onDrop($event, 1)" @dragenter.prevent @dragover.prevent>
                  <div v-for="item in getList(1)" :key="item.id" class="identified drag-el-horizontal" draggable="true"
                       @dragstart="startDrag($event, item)">
                    <p>{{ item.word }}</p>
                  </div>
                </div>
              </div>
              <div class="unidentified drag-el-horizontal" draggable="false">
                <p style="font-weight: 500; padding-left: 15px; font-size:medium;">Signs contract</p>

                <div class="drop-zone" @drop="onDrop($event, 2)" @dragenter.prevent @dragover.prevent>
                  <div v-for="item in getList(2)" :key="item.id" class="identified drag-el-horizontal" draggable="true"
                       @dragstart="startDrag($event, item)">
                    <p>{{ item.word }}</p>
                  </div>
                </div>
              </div>

            </div>
          </div>
        </div>

        <div class="flex-boxes">
          <div class="outer-box" :style="{display: visibility[1] ? 'block' : 'none'}">

            <h3>Clerk</h3>
            <div class="flex-between">
              <div class="unidentified drag-el-horizontal" draggable="false">
                <p style="font-weight: 500; padding-left: 15px; font-size:medium;">Locates the vehicle</p>

                <div class="drop-zone" @drop="onDrop($event, 3)" @dragenter.prevent @dragover.prevent>
                  <div v-for="item in getList(3)" :key="item.id" class="identified drag-el-horizontal" draggable="true"
                       @dragstart="startDrag($event, item)">
                    <p>{{ item.word }}</p>
                  </div>
                </div>
              </div>
              <div class="unidentified drag-el-horizontal" draggable="false">
                <p style="font-weight: 500; padding-left: 15px; font-size:medium;">Marks the contract active</p>

                <div class="drop-zone" @drop="onDrop($event, 4)" @dragenter.prevent @dragover.prevent>
                  <div v-for="item in getList(4)" :key="item.id" class="identified drag-el-horizontal" draggable="true"
                       @dragstart="startDrag($event, item)">
                    <p>{{ item.word }}</p>
                  </div>
                </div>
              </div>

            </div>
          </div>

          <div class="outer-box" :style="{display: visibility[2] ? 'block' : 'none'}">
            <h3>Vehicle</h3>
            <div class="flex-between">
              <div class="unidentified drag-el-horizontal" draggable="false">
                <p style="font-weight: 500; padding-left: 15px; font-size:medium;">Give the keys</p>
                <div class="drop-zone" @drop="onDrop($event, 5)" @dragenter.prevent @dragover.prevent>
                  <div v-for="item in getList(5)" :key="item.id" class="identified drag-el-horizontal" draggable="true"
                       @dragstart="startDrag($event, item)">
                    <p>{{ item.word }}</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div class="outer-box" :style="{display: visibility[3] ? 'block' : 'none'}">
          <h3>Reservation</h3>
          <div class="flex-between">
            <div class="unidentified drag-el-horizontal" draggable="false">
              <p style="font-weight: 500; padding-left: 15px; font-size:medium;">Vehicle type is not available</p>
              <div class="drop-zone" @drop="onDrop($event, 6)" @dragenter.prevent @dragover.prevent>
                <div v-for="item in getList(6)" :key="item.id" class="identified drag-el-horizontal" draggable="true"
                     @dragstart="startDrag($event, item)">
                  <p>{{ item.word }}</p>
                </div>
              </div>
            </div>
          </div>
        </div>

      </div>

      <div class="outer-box">
        <h3>Fine Tuned Verbs</h3>
        <div class="flex-between">
          <div class="unidentified drag-el-horizontal" draggable="false">
            <div class="drop-zone" @drop="onDrop($event, 13)" @dragenter.prevent @dragover.prevent>
              <div v-for="item in getList(13)" :key="item.id" class="unidentified drag-el-horizontal" draggable="true"
                   @dragstart="startDrag($event, item)">
                <p>{{ item.word }}</p>
              </div>
            </div>
          </div>
        </div>
      </div>

    </div>
  </main>
  <footer>
    <div id="buttons" class="relative">
      <button class="navitem" @click="validateCustomer()" :style="{display: visibility[0] ? 'block' : 'none'}">Next
      </button>
      <button class="navitem" @click="validateClerk()" :style="{display: visibility[1] ? 'block' : 'none'}">Next
      </button>
      <button class="navitem" @click="validateVehicle()" :style="{display: visibility[2] ? 'block' : 'none'}">Next
      </button>
      <button class="navitem" @click="validateReservation()" :style="{display: visibility[3] ? 'block' : 'none'}">Next
      </button>
    </div>
  </footer>
</template>

