import { createRouter, createMemoryHistory} from "vue-router";
import PageOne from "../views/PageOne.vue";
import PageTwo from "../views/PageTwo.vue";
import AnimTwo from "../views/AnimTwo.vue";
import PageThree from "../views/PageThree.vue";
import PageZero from "../views/PageZero.vue";
import ScreenZero from "../views/ScreenZero.vue";
import CongratulationsPage from "../views/CongratulationsPage.vue";
import PageOneNew from '../views/PageOneNew.vue'

const router = createRouter({
  history: createMemoryHistory(),
  routes: [
    {
      path: "/",
      name: "screen-zero",
      component: ScreenZero,
    },
    {
      path: "/page-zero",
      name: "page-zero",
      component: PageZero,
    },
    {
      path: "/anim-two",
      name: "anim-two",
      component: AnimTwo,
    },
    {
      path: "/screen-one",
      name: "page-one",
      component: PageOne,
    },
    {
      path: "/screen-one-new",
      name: "page-one-new",
      component: PageOneNew,
    },
    {
      path: "/screen-two",
      name: "page-two",
      component: PageTwo,
    },
    {
      path: "/screen-three",
      name: "page-three",
      component: PageThree,
    },
    {
      path: "/completion",
      name: "Congratulations",
      component: CongratulationsPage,
    },
  ],
});

export default router;
